<template>
  <div id="app">
    <p class="text-red">Welcome</p>
    <div>
      Hi~ {{profile.displayName}}
    </div>
    請確認以下是你本人嗎?
    <br>
    <img :src="profile.pictureUrl" height="200px">
    <button @click="confirm(1)">YES</button>
    <button @click="confirm(0)">NO</button>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      profile: {}
    }
  },
  beforeCreate () {
    window.liff
      .init({
        liffId: '1657816678-d6o85QZa'
      }).then(() => {
        this.getprofile()
      })
  },
  methods: {
    confirm (num) {
      if (num === 0) {
        alert('錯了吧')
      } else {
        alert('不錯喔~')
      }
    },
    getprofile () {
      window.liff
        .getProfile()
        .then((profile) => {
          this.profile = profile
        })
    }
  }
}
</script>

<style>
.text-red{
  color: red;
  font-weight: 900;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



